.sticker {
    width: 100px;
    height: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: white;
    border: 2px solid #082ebd;
    border-radius: 25px;
    padding: 10px;
    position: relative;
    overflow: hidden;
    transition: background-color 0.6s ease;
  
    img.icon-image {
      width: 100%;
      height: 100%;
      border-radius: 25px;
      transition: opacity 0.6s ease;
    }
  
    &:hover {
      background-color: black;
  
      img.icon-image {
        opacity: 0.3;
      }
  
      .sticker-name {
        opacity: 1;
      }
    }
  
    .sticker-name {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      color: white;
      font-size: 16px;
      text-align: center;
      opacity: 0;
      transition: opacity 0.6s ease;
    }
  }
  