/* Base styles */
form {
    max-width: 400px;
    margin: 0 auto;
    padding: 20px;
    background-color: #f9f9f9;
    border: 1px solid #ddd;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  
    label {
      font-weight: bold;
      display: block;
      margin-bottom: 8px;
    }
  
    input[type='email'],
    textarea {
      width: 92%;
      padding: 10px;
      margin-bottom: 15px;
      border: 1px solid #ccc;
      border-radius: 5px;
      font-size: 16px;
  
      textarea {
        min-height: 150px;
      }
    }
  
    button[type='submit'] {
      background-color: #082ebd;
      color: white;
      border: none;
      padding: 12px 20px;
      font-size: 16px;
      cursor: pointer;
      border-radius: 5px;
      transition: background-color 0.3s ease;
      width: 100%;
  
      &:hover {
        background-color: #0056b3;
      }
    }
  
    .envoi {
      text-align: center;
    }
  
    .error {
      color: #dc3545;
      margin-bottom: 15px;
      font-size: 14px;
    }
  
    .confirmation-message {
      border: 2px solid #082ebd;
      background-color: #f1f1f1;
      border-radius: 25px;
      padding: 10px;
      margin-top: 10px;
    }
  }
  
  /* Responsive  */
  @media (max-width: 768px) {
    form {
      padding: 15px;
    }
  }
  
  @media (max-width: 600px) {
    form {
      input[type='email'],
      textarea {
        width: 100%;
      }
    }
  }
  